/* basic elements */
html {
	margin: 0;
	padding: 0;
	}
p {
	margin-top: 0;
	text-align: justify;
	}
h3 {
	font: italic normal 1.4em georgia, sans-serif;
	letter-spacing: 1px;
	margin-bottom: 0;
	color: #7D775C;
	}
a:link {
	font-weight: bold;
	text-decoration: none;
	color: #B7A5DF;
	}
a:visited {
	font-weight: bold;
	text-decoration: none;
	color: #D4CDDC;
	}
a:hover, a:focus, a:active {
	text-decoration: underline;
	color: #9685BA;
	}
abbr {
	border-bottom: none;
	}

/* --------------------------------

xBackground

-------------------------------- */

body::before, body, body::after {
	width:100%;
  height:100%;
}

body {
	z-index: 0;
	background: #000 url('./assets/stars.png') repeat top center;
}

body::before {
  content:'';
	position: fixed;
	display:block;
	z-index: 1;
	background:transparent url('./assets/twinkling.png') repeat top center;
	animation: move-twink-back 200s linear infinite;
}

body::after {
  content:'';
	position: fixed;
	display:block;
	z-index: 2;
  background:transparent url('./assets/clouds.png') repeat top center;
	animation: move-clouds-back 200s linear infinite;
}

body > * {
  z-index: 3;
  position: relative;
}

@keyframes move-twink-back {
	from {background-position:0 0;}
	to {background-position:-10000px 5000px;}
}

@keyframes move-clouds-back {
	from {background-position:0 0;}
	to {background-position:10000px 0;}
}

